import React from 'react';
import { Button, ListGroup, ListGroupItem } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import "../css/formation.css";
import { Icon } from '../components/index.ts';

function Formation() {

    function HeaderList(){
        return (
          <ListGroup as={"ul"}>
            <ListGroup.Item as={"li"}><Link to={"/#top"}><Button variant='link'>Accueil</Button></Link></ListGroup.Item>
            <ListGroup.Item as={"li"} id='apropos'>
              <Button variant='link'>A propos...</Button>
              <ListGroup as={"ul"} className='submenu'>
                <ListGroup.Item as={"li"}><Link to={"/qui"}><Button variant='link'>Qui suis-je ?</Button></Link></ListGroup.Item>
                <ListGroup.Item as={"li"}><Link to={"/bureau"}><Button variant='link'>Consultation</Button></Link></ListGroup.Item>
              </ListGroup>
            </ListGroup.Item>
            <ListGroup.Item as={"li"} active={true}><Button variant='link' href='#top'>Formation</Button></ListGroup.Item>
            <ListGroup.Item as={"li"}><Link to={"/#formManager"}><Button variant='link'>Contactez-nous</Button></Link></ListGroup.Item>
          </ListGroup>
        )
    }

    function FooterComponent(){
        return (
          <footer>
            <div className='left'>
              <Link to={"/qui"}><Button variant='link'>Entreprise</Button></Link>
              <Link to={"/avis"}><Button variant='link'>Avis des clients</Button></Link>
              <a href='#top'><Button variant='link'>A propos</Button></a>
            </div>
            <div className='middle'>
              <Link><Button variant='link'>Formation</Button></Link>
              <Link to={"/blog"}><Button variant='link'>Blog</Button></Link>
              <Link to={"/rgpd"}><Button variant='link'>RGPD</Button></Link>
            </div>
            <div className='right'>
              <Link to={"/cgv"}><Button variant='link'>Conditions</Button></Link>
              <Link to={"/politique"}><Button variant='link'>Politiques</Button></Link>
              <Link to={"/cookies"}><Button variant='link'>Paramtres de cookies</Button></Link>
            </div>
          </footer>
        )
      }

    return (<div className='formation App'>
        <div id='top'></div>
        <header>
            <div className='HeaderTop'>
            <h1>Scarabe Dor</h1>
            </div>
            <div className='HeaderBottom'>
            <HeaderList/>
            </div>
        </header>
        <main>
            <h1>Formation</h1>
            <section id='main' className='d-flex flex-column'>
                <div id="part1" className='d-flex flex-column align-items-center'>
                    <span className='align-self-lg-start'>Je me charge d'organiser des formations sur diffrentes thmatiques : </span>
                    <ListGroup as={"ul"}>
                        <ListGroupItem as={"li"}>Confiance de soi et communication</ListGroupItem>
                        <ListGroupItem as={"li"}>Gestion du stress</ListGroupItem>
                        <ListGroupItem as={"li"}>Gestion des priorits</ListGroupItem>
                        <ListGroupItem as={"li"}>Gestion des motions</ListGroupItem>
                    </ListGroup>
                </div>
                <div className='mt-3'>
                    <span>Cette mthode vous confrera une opportunit de prendre du recul sur vos manires d'agir, pour exprimenter, dans un cadre respectueux, de nouvelles faons de nous comporter, pour notre bien et celui des autres.
                    Dans le meilleur des cas, vous prendrez conscience des changement que vous devrez apporter sur vous-mme !</span>
                    <br /><br />
                    <span>Les apports thoriques sappuient sur les mises en situation vcues par le groupe, pour faciliter leur ancrage.</span>
                    <br /><br />
                    <span>J'interviens en inter-entreprise et en intra. Les formations ont lieu dans le centre ou vos locaux. Contactez-moi pour solliciter mes services: </span>
                </div>
                <div className="mt-3">
                    <div className="contacts">
                        <div className="contact">
                            <Icon size='40px' name={"bxs-envelope"}/>
                            <Button variant='link' href='mailto:scarabee.dore21@gmail.com'>scarabee.dore21@gmail.com</Button>
                        </div>
                        <div className="contact">
                            <Icon size='40px' name={"bxs-phone"}/>
                            <Button variant='link' href='tel:0760231984'>(+33)7.60.23.19.84</Button>
                        </div>
                    </div>
                </div>
            </section>
        </main>
        <FooterComponent/>
    </div>);
}

export default Formation;