import React from 'react';
import { ListGroup,Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';

function Cookies() {

    function HeaderList(){
        return (
          <ListGroup as={"ul"}>
            <ListGroup.Item as={"li"} active={true}><Link to={"/#top"}><Button variant='link'>Accueil</Button></Link></ListGroup.Item>
            <ListGroup.Item as={"li"} id='apropos'>
              <Button variant='link'>A propos...</Button>
              <ListGroup as={"ul"} className='submenu'>
                <ListGroup.Item as={"li"}><Link to={"/qui"}><Button variant='link'>Qui suis-je ?</Button></Link></ListGroup.Item>
                <ListGroup.Item as={"li"}><Link to={"/bureau"}><Button variant='link'>Consultation</Button></Link></ListGroup.Item>
              </ListGroup>
            </ListGroup.Item>
            <ListGroup.Item as={"li"}><Link to={"/formation"}><Button variant='link'>Formation</Button></Link></ListGroup.Item>
            <ListGroup.Item as={"li"}><Link to={"/#formManager"}><Button variant='link'>Contactez-nous</Button></Link></ListGroup.Item>
          </ListGroup>
        )
    }

    function FooterComponent(){
        return (
          <footer>
            <div className='left'>
              <Link to={"/qui"}><Button variant='link'>Entreprise</Button></Link>
              <Link to={"/avis"}><Button variant='link'>Avis des clients</Button></Link>
              <a href='#top'><Button variant='link'>A propos</Button></a>
            </div>
            <div className='middle'>
              <Link to={"/formation"}><Button variant='link'>Formation</Button></Link>
              <Link to={"/blog"}><Button variant='link'>Blog</Button></Link>
              <Link to={"/rgpd"}><Button variant='link'>RGPD</Button></Link>
            </div>
            <div className='right'>
              <Link to={"/cgv"}><Button variant='link'>Conditions</Button></Link>
              <Link to={"/politique"}><Button variant='link'>Politiques</Button></Link>
              <Button variant='link' href='#top'>Paramtres de cookies</Button>
            </div>
          </footer>
        )
      }

    return (<div className='cookies App'>
        <div id='top'></div>
        <header>
            <div className='HeaderTop'>
            <h1>Scarabe Dor</h1>
            </div>
            <div className='HeaderBottom'>
            <HeaderList/>
            </div>
        </header>
        <main>
            <h1 className='mb-4'>Paramtres de cookies</h1>
            <p>En visitant le site Web et en consultant les pages, vous comprenez et acceptez la faon dont nous traitons les donnes personnelles conformment  notre politique de confidentialit. Nous respectons votre vie prive et le droit de contrler vos donnes personnelles.</p> <br /><br />
            <p>Les cookies sont de petites quantits dinformations stockes dans des fichiers au sein du navigateur utilis. Les cookies sont accessibles et enregistrs par les sites internet que vous consultez, et par les socits qui affichent leurs annonces publicitaires sur des sites internet, pour quils puissent reconnatre le navigateur. Les sites Internet peuvent uniquement accder aux cookies quils ont stock sur votre ordinateur, et pas autrement.</p> <br /><br />
            <p>Voici les principaux usages des cookies: </p>
            <ul>
              <li>Reconnatre le navigateur utilis et enregistrer les information dtermines (infos de connexion, etc...)</li>
              <li>Grer des connexion  des services tiers (Rseaux sociaux...)</li>
              <li>Cibler l'internaute qui visite le site</li>
              <li>Dterminer les statistiques de frquentation du site Web</li>
            </ul> <br /><br />
            <p>Nous vous rappelons que vous tre libre de configurer le navigateur pour accepter les cookies, refuser certains ou tous les bloquer.</p>
        </main>
        <FooterComponent/>
    </div>);
}

export default Cookies;