import React from 'react';
import { ListGroup,Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';

function Politique() {

    function HeaderList(){
        return (
          <ListGroup as={"ul"}>
            <ListGroup.Item as={"li"} active={true}><Link to={"/#top"}><Button variant='link'>Accueil</Button></Link></ListGroup.Item>
            <ListGroup.Item as={"li"} id='apropos'>
              <Button variant='link'>A propos...</Button>
              <ListGroup as={"ul"} className='submenu'>
                <ListGroup.Item as={"li"}><Link to={"/qui"}><Button variant='link'>Qui suis-je ?</Button></Link></ListGroup.Item>
                <ListGroup.Item as={"li"}><Link to={"/bureau"}><Button variant='link'>Consultation</Button></Link></ListGroup.Item>
              </ListGroup>
            </ListGroup.Item>
            <ListGroup.Item as={"li"}><Link to={"/formation"}><Button variant='link'>Formation</Button></Link></ListGroup.Item>
            <ListGroup.Item as={"li"}><Link to={"/#formManager"}><Button variant='link'>Contactez-nous</Button></Link></ListGroup.Item>
          </ListGroup>
        )
    }

    function FooterComponent(){
        return (
          <footer>
            <div className='left'>
              <Link to={"/qui"}><Button variant='link'>Entreprise</Button></Link>
              <Link to={"/avis"}><Button variant='link'>Avis des clients</Button></Link>
              <a href='#top'><Button variant='link'>A propos</Button></a>
            </div>
            <div className='middle'>
              <Link to={"/formation"}><Button variant='link'>Formation</Button></Link>
              <Link to={"/blog"}><Button variant='link'>Blog</Button></Link>
              <Link to={"/rgpd"}><Button variant='link'>RGPD</Button></Link>
            </div>
            <div className='right'>
              <Link to={"/cgv"}><Button variant='link'>Conditions</Button></Link>
              <Button variant='link' href='#top'>Politiques</Button>
              <Link to={"/cookies"}><Button variant='link'>Paramtres de cookies</Button></Link>
            </div>
          </footer>
        )
      }

    return (<div className='politique App'>
        <div id='top'></div>
        <header>
            <div className='HeaderTop'>
            <h1>Scarabe Dor</h1>
            </div>
            <div className='HeaderBottom'>
            <HeaderList/>
            </div>
        </header>
        <main>
            <h1 className='mb-4'>Politiques de confidentialit</h1>
            <h2>Les informations pouvant tre collectes</h2>
            <p>Informations personnelles (nom,adresse,email...), navigateur Web, fuseau horaire et certains cookies...</p>
            <h2>Vos droits</h2>
            <ul className='conditions-list'>
              <li>tre inform sur vos donnes</li>
              <li>Consulter vos donnes</li>
              <li>Demander la correction de vos donnes</li>
              <li>Supprimer vos donnes</li>
            </ul>
            <h2>Divulgation lgale</h2>
            <p>Je divulgerai toute information que je collecte et utilise si la loi lexige ou lautorise, par exemple pour me conformer  une citation  comparatre,  une procdure judiciaire similaire ou rpondre  une demande du gouvernement.</p>
            <p>Si vous avez quelconque rclamation concernant vos donnes personnelles, veuillez me contacter: <Button variant='link' href='mailto:scarabee.dore21@gmail.com'>scarabee.dore21@gmail.com</Button> ou <Button variant='link' href='tel:0760231984'>07.60.23.19.84</Button></p>
        </main>
        <FooterComponent/>
    </div>);
}

export default Politique;