import React from 'react';
import { ListGroup,Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';

function RGPD() {

    function HeaderList(){
        return (
          <ListGroup as={"ul"}>
            <ListGroup.Item as={"li"} active={true}><Link to={"/#top"}><Button variant='link'>Accueil</Button></Link></ListGroup.Item>
            <ListGroup.Item as={"li"} id='apropos'>
              <Button variant='link'>A propos...</Button>
              <ListGroup as={"ul"} className='submenu'>
                <ListGroup.Item as={"li"}><Link to={"/qui"}><Button variant='link'>Qui suis-je ?</Button></Link></ListGroup.Item>
                <ListGroup.Item as={"li"}><Link to={"/bureau"}><Button variant='link'>Consultation</Button></Link></ListGroup.Item>
              </ListGroup>
            </ListGroup.Item>
            <ListGroup.Item as={"li"}><Link to={"/formation"}><Button variant='link'>Formation</Button></Link></ListGroup.Item>
            <ListGroup.Item as={"li"}><Link to={"/#formManager"}><Button variant='link'>Contactez-nous</Button></Link></ListGroup.Item>
          </ListGroup>
        )
    }

    function FooterComponent(){
        return (
          <footer>
            <div className='left'>
              <Link to={"/qui"}><Button variant='link'>Entreprise</Button></Link>
              <Link to={"/avis"}><Button variant='link'>Avis des clients</Button></Link>
              <a href='#top'><Button variant='link'>A propos</Button></a>
            </div>
            <div className='middle'>
              <Link to={"/formation"}><Button variant='link'>Formation</Button></Link>
              <Link to={"/blog"}><Button variant='link'>Blog</Button></Link>
              <Button variant='link' href='#top'>RGPD</Button>
            </div>
            <div className='right'>
              <Link to={"/cgv"}><Button variant='link'>Conditions</Button></Link>
              <Link to={"/politique"}><Button variant='link'>Politiques</Button></Link>
              <Link to={"/cookies"}><Button variant='link'>Paramtres de cookies</Button></Link>
            </div>
          </footer>
        )
      }

    return (<div className='rgpd App'>
        <div id='top'></div>
            <header>
                <div className='HeaderTop'>
                <h1>Scarabe Dor</h1>
                </div>
                <div className='HeaderBottom'>
                <HeaderList/>
                </div>
            </header>
            <main>
                <h1 className='mb-4'>RGPD et la Confidentialit</h1>
                <h2>En vigueur au 15/11/2022</h2>
                <p className='mt-5'>Conformment aux dispositions des Articles 6-III et 19 de la Loi n2004-575 du 21 juin 2004 pour la Confiance dans lconomie numrique, dite L.C.E.N., il est port  la connaissance des utilisateurs et visiteurs, ci-aprs lUtilisateur , de ce site , ci-aprs le  Site , les prsentes mentions lgales. <br /><br />

La connexion et la navigation sur le Site par lUtilisateur implique acceptation intgrale et sans rserve des prsentes mentions lgales. <br /><br />

Ces dernires sont accessibles sur le Site  la rubrique  Mentions lgales .</p>
            </main>
            <FooterComponent/>
    </div>);
}

export default RGPD;